import React from 'react';
import "./About.css";
import perfil from "../../media/fotoPerfil.png"; 

const About = () => {
  return (
    <div className='about-container'>
        
        <div className='about-desc'>
            <h3>About Me</h3>
            <p>
            I am a web developer interested in working with new technologies.
            
            <br />
          
            I work with: Js, Git, React, Bootstrap, Redux, HTML y CSS.
            </p>
        </div>

        <div className='about-img'>
        <img src={perfil}  alt="Perfil" />
        </div>

    </div>
  )
}

export default About