import React from "react";
import "./Slider.css";
import Alkemy from "../../media/sliderAlkemy.png"; 
import Html from "../../media/sliderHtml.png"; 
import Cv from "../../media/sliderCv.png"; 

const slidesInfo = [
  {
    src: Alkemy,
    alt: "Project 1",
    desc: "Alkemy Challenger React",
    href:"https://github.com/federburgos/Alkemy-Challenger-React",
  },
  {
    src: Cv,
    alt: "Project 2",
    desc: "Curriculum Vitae Online",
    href:"https://github.com/federburgos/CVOnline-FedericoBurgos",
  },
  {
    src: Html,
    alt: "Project 3",
    desc: "Trabajo Final Html y Css",
    href:"https://github.com/federburgos/TrabajoFinal-HTML-CSS",
  
  },
];




const slides = slidesInfo.map((slide) => (
  <div className="slide-container">
    <a href={slide.href} target="_blank"><img src={slide.src} alt={slide.alt} /></a>
    <div className="slide-desc">
      <span>{slide.desc}</span>
    </div>
  </div>
));

export default slides;