import React from 'react';
import "./info.css";

const Info = () => {
  return (
    <div className='info-container'>
        <div className='info'>
          <div className='contenedor'>
           
            <h3>It's like everyone tells a story about themselves inside their own head. Always. All the time. That story makes you what you are. We build ourselves out of that story.</h3>
            
            <br />
            
            <p>Patrick Rothfuss | The Name of the Wind</p>
          </div>
        </div>
    </div>
  )
}

export default Info